import React from 'react'
import styled from 'styled-components'
import styles from './obt.module.scss'

import BigNumber from 'bignumber.js'

const Styles = styled.div`

  overflow-y: scroll;
  height: 100%;
  
  &::-webkit-scrollbar {
    display:none;
  }

  table {
    border-spacing: 0;
  
    height: 500px;

    tr {
      :last-child {
        td {
          border-bottom: 0;
        }
      }
    }

    th,
    td {
      margin: 0;
      padding-top: 3px;

      text-align: right;

      :last-child {
      }
    }

    .ask_row {
      color: #00BBA8;
    }

    .bid_row {
      color: #FF5677;
    }

  }
`
export interface OrderBookTableProps {
  depth: any
}

const getAmt = (rawAmt: any, precision: number = 4) => {
  return new BigNumber(rawAmt).dividedBy(1e+18).toNumber().toFixed(precision)
}

const Table = ({ depth }: OrderBookTableProps) => {

  return (
    <Styles>
      <table>
        <thead>
        </thead>
        <tbody>
          {
            depth && depth.asks &&
            depth.asks.reverse().map((ask: any, ask_index: number) => {
              const amt = getAmt(ask[1])
              return (
                <tr className={'ask_row'} key={ask_index}>
                  <td className={styles.ab_col1}>{ask[0]}</td>
                  <td className={styles.ab_col2}>{amt}</td>
                  <td className={styles.ab_col3}>{ask[2]}</td>
                </tr>
              )
            })
          }
          {
            depth && depth.bids &&
            depth.bids.map((bid: any, bid_index: number) => {
              const amt = getAmt(bid[1])
              return (
                <tr className={'bid_row'} key={bid_index}>
                  <td className={styles.ab_col1}>{bid[0]}</td>
                  <td className={styles.ab_col2}>{amt}</td>
                  <td className={styles.ab_col3}>{bid[2]}</td>
                </tr>
              )
            })
          }
        </tbody>
      </table>
    </Styles>
  )
}

export default Table
