import i18n from 'i18next'
import { initReactI18next } from 'react-i18next'

import zhCommon from 'locales/zh_CN/common'
import enCommon from 'locales/en_US/common'

import zhMenu from 'locales/zh_CN/menu'
import enMenu from 'locales/en_US/menu'

import zhTradingPage from 'locales/zh_CN/trading_page'
import enTradingPage from 'locales/en_US/trading_page'

const resources = {
  zh_CN: {
    common: {
      ...zhCommon,
      ...zhMenu,
    },
    pages: {
      ...zhTradingPage
    }
  },
  en_US: {
    common: {
      ...enCommon,
      ...enMenu,
    },
    pages: {
      ...enTradingPage
    }
  },
};

i18n
  .use(initReactI18next) // passes i18n down to react-i18next
  .init({
    resources,
    lng: 'en_US',

    keySeparator: false, // we do not use keys in form messages.welcome

    interpolation: {
      escapeValue: false, // react already safes from xss
    },
  });

export default i18n;
